.app-stars {
	--star-width: 12px;
	--margin-star: 4px;

	&__inner {
		position: relative;
		//   ,       
		margin: 0 calc(var(--margin-star) / -2);
	}

	&__grey-stars,
	&__yellow-stars {
		display: flex;
	}

	&__grey-stars {
	}

	&__yellow-stars {
		position: absolute;
		top: 0;
		left: 0;

		overflow: hidden;
	}

	&__star {
		//       ,      app-stars__yellow-stars ( )
		width: calc(var(--star-width) + var(--margin-star));
		height: 12px;
		flex-shrink: 0;
	}
}
